import {useState,useEffect} from 'react'
import Test from './Test'
import './style.css'

function App() {
  const [ser,setSer] = useState('')
  useEffect(()=>{
    f().then(i=>setSer(i.message))

  },[])

  async function f(){
    const data = await fetch('api')
    const res = data.json()

    return res
  } 
  return (
    <div className="App">
     <h1>React</h1>
     <p>{ser}</p>
     <Test/>
    </div>
  );
}

export default App;
